<template>
  <div class="regist-container" id="regist-container">
      <canvas id="star" width="100%" height="100%"></canvas>
      <el-form autoComplete="off" :model="registForm" :rules="rules" ref="registForm" label-position="left" label-width="80px"
      class="card-box regist-form">
      <h3 class="title">平台注册</h3>
      <el-form-item label="用户名" prop="name">
        <el-input name="name" type="text" v-model="registForm.name" placeholder="用户名"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="phone">
        <el-input name="phone" type="text" v-model="registForm.phone" autoComplete="off" placeholder="手机号码"></el-input>
      </el-form-item>
      <el-form-item label="验证码" prop="phone">
        <el-input name="phone" type="text" v-model="registForm.code" autoComplete="off" placeholder="验证码" class="code-wrap"></el-input>
        <el-button class="code-btn" name="codeBtn" :loading="codeLoading" @click.native.prevent="getCode">{{btnText}}</el-button>
      </el-form-item>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input name="password" type="password"  v-model="registForm.password" autoComplete="off"
          placeholder="密码(3-10个数字或字母)"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleRegist">
          注册
        </el-button>
      </el-form-item>
      <div class="tips">
            <router-link to="/login" class="fr">有账号？立即登录</router-link>
      </div>
    </el-form>
  </div>
</template>

<script>
  import { validatePhone,validatePass } from 'utils/validate';
  require("star"); 
  export default {
    components: {  },
    name: 'regist',
    data() {
      const isPhone = (rule, value, callback) => {
        if (!validatePhone(value)) {
          callback(new Error('请输入正确的合法手机号码'));
        } else {
          callback();
        }
      };
      const isPass = (rule, value, callback) => {
        if (!validatePass(value)) {
          callback(new Error('密码格式3-10位数字或字母'));
        } else {
          callback();
        }
      };
      return {
        "registForm":{
          phone:"",
          password:"",
          code:"",
          name:""
        },
        "rules":{
          name:[
            { required: true,min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
          ],
          password:[
            { required: true, trigger: 'blur' ,validator:isPass}
          ],
          code:[
            { required: true, message: '请输入验证码', trigger: 'blur' },
          ],
          phone:[
            { required: true, trigger: 'blur' ,validator:isPhone}
          ]
        },
        btnText:"获取验证码",
        loading: false,
        codeLoading:false
      }
    },
    methods: {
      handleRegist() {
        this.$refs.registForm.validate(valid => {
          if (valid) {
            this.loading = true;
            this.$store.dispatch('Regist', this.registForm).then((res) => {
              this.loading = false;
              if(res.code==="0000"){
                this.$msgbox({
                  showCancelButton: true,
                  confirmButtonText: '去登录',
                  cancelButtonText: '取消',
                  message:"恭喜您注册成功！是否立即登录",
                  beforeClose: (action, instance, done) => {
                    if(action=="confirm"){
                      done();
                      setTimeout(()=>{
                        this.$router.push({path:"/login"})
                      },500)
                    }else{
                      done();
                      this.$refs["registForm"].resetFields();
                    }
                  }
                });
              }else{
                this.$message({message:res.message,type:"error"});
              }
            }).catch(err => {
              this.$message.error(err);
              this.loading = false;
            });
          } else {
              this.loading = false;
              return false;
          }
        });
      },
      getCode(){
        this.$refs["registForm"].validateField("phone",(valid)=>{
            if(!valid){
              let phone = this.registForm.phone;
              
              let count = 60;
              const _text = this.btnText;
              this.codeLoading=true;
              let timer = setInterval(()=>{
                if(count>0){            
                  this.btnText=(--count)+"秒";
                }else{
                  count = 60;
                  this.btnText=_text;
                  clearInterval(timer);
                  this.codeLoading=false;
                }
              },1000);

              this.$store.dispatch("GetCode",this.registForm.phone).then((res)=>{
                  if(res.code==="0000"){
                    this.$message({message:"短信验证码发送成功,请注意查收",duration:1000*10,showClose:true});
                    this.codeLoading=false;
                  }else{
                    this.$message({message:"发送失败",duration:1000*10,showClose:true,type:"error"});
                    clearInterval(timer);
                    this.btnText="重新获取";
                    this.codeLoading=false;
                  }
              });


            }
        });
      }
    },
    created() {
      console.log("created");
    },
    mounted(){
      var window_width = $(window).width();
      var window_height = $(window).height();
      $('#star').regAnimation({
          window_width: window_width,
          window_height: window_height,
          window_background: '#fff',
          star_count: '100',
          star_color: '#02c5ff',
          star_depth: '600'
      });
      console.log( $(this.$el) )

    },
    destroyed() {
      console.log("destroyed");
    }
  }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import "src/styles/mixin.scss";
  .tips {
    font-size: 14px;
    margin-bottom: 5px;
    color: #333;
  }

  .regist-container {
    @include relative;
    height: 100vh;
    #star{
      position: absolute;
      left: 0;
      right: 0;
    }
    .regist-form {
      position: absolute;
      left: 0;
      right: 0;
      width: 500px;
      padding: 35px 35px 15px 35px;
      margin: 120px auto;
    }
    .title {
      font-size: 26px;
      font-weight: 400;
      color: #607D8B;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
    .code-wrap{
      float:left;
      width: 200px;
    }
    .code-btn{
      width: 120px;
      float: right;;
    }
  }
</style>



